<!--Title: CIS2286 - Assigment 6 Task Scheduler-->

<template>
  <div class="container">
    <Header @delete-all="deleteAll" @add-task="addTask" />
    <Tasks @delete-task="deleteTask" :tasks="tasks" />
  </div>

</template>

<script>
import Header from "@/components/Header";
import Tasks from "@/components/Tasks";




export default {
  name: 'App',
  components: {
    Header,
    Tasks,
  },
  data() {
    return {
      tasks: [],
    }
  },
  methods: {
    deleteTask(id) {
      this.tasks = this.tasks.filter((task) => task.id !== id)
    },
    deleteAll() {
      this.tasks = [];
    },
    addTask() {
      let taskName = prompt("Enter task name: ");
      let taskDate = prompt("Enter task date: ");

      this.tasks.push({id: Math.floor(Math.random()*100000), text:taskName, day:taskDate, reminder:true});

    }
  },
  created() {
    this.tasks = [
      {
        id: 1,
        text: 'CIS Final Test',
        day: '2022-04-22',
        reminder: true,
      },
      {
        id: 2,
        text: 'Go to the beach',
        day: '2022-04-24',
        reminder: true,
      },
    ]
  }
}
</script>

